@import '_var';



body{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
  
}
button:focus, input:focus, textarea:focus, select:focus {
    outline: none;
  }

// index page
.logo{
    height: 50px;
    @media only screen and (max-width:400px){
        height: 40px;
    }

}
.blue{
    background-color: $theBlue;
}
.dropdown-menu{
    left: -78px !important;
}
.menu{

        margin: 20px;
        text-decoration: none;
        font-size: medium;
        color: black;
        &:hover{
            color: $orange;
            border-bottom: 3px solid $orange;
            text-decoration: none;
        }
 
   
}
.menu_1{
    @media only screen and (max-width:600px){
        display: none;
    }
}
.menu_2{
    display: none;
    @media only screen and (max-width:600px){
        display: block;
    }
}
// sliders
.carousel-item{
    img{
        height: 800px;
        object-fit: cover;
    }
}
.tasks{
    font-size:large;
    i{
        color: $orange;
        font-size: xx-large;
    }
}
// section 3
.bg-img{
    @media only screen and (max-width:1000px){
        flex-direction: column;
    }
    img{
        width: 50%;
        height: 400px;
        object-fit: cover;
        @media only screen and (max-width:1000px){
            width: 100%;
        }
    }
    .bg-svg{
        width: 50%;
        height: 400px;
        background-color: $orange;
        @media only screen and (max-width:1000px){
            width: 100%;
        }
        
        h1, p{
            font-weight: 700;
            text-align: center;
            margin: auto;
            line-height: 35px;
        }
    }
}
.bg-img2{
    background-image: url('../img/smolesection.jpg');
    object-fit: cover;
    

    .bg-color{
        background-color: $simpleBlue;
        color: white;
        h4{
            margin: auto;
            width: 60%;
            text-align: center;
            font-weight: 600;
            line-height: 50px;
        }
    }
}
// contact us section

.contactUs{
    font-weight: 700;
    border-bottom: 8px solid $orange;
    width: fit-content;
    margin: 30px auto;
    color: $theBlue;
}
.inputs_mail_sub{
    font-weight: 600;

    input{
        width: 100%;
        height: 50px;
        border-radius: 6px;
        border: 0.5px solid $simpleBlue;

     

    }
   
}
.description{
    font-weight: 600;
    .textarea{
        width: 100% !important;
        border-radius: 6px;
        border: 0.5px solid $simpleBlue;
    }
}
.btn-1{
    width: 150px;
    margin: 20px auto;
    
}
.contacts{
    width: 40%;
    margin: auto;

    @media only screen and (max-width:400px){
        width: 60%;
    }

}
// login pop up

.pop-up-add_events {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.473);
    z-index: 1111;
    display: flex;
    justify-content: center;
  }

  
  .pop-up-add_event {
    width: 25%;
    height: auto;
    background-color: white;
    padding: 25px;
    position: fixed;
    top: 6%;
    border-radius: 5px;
    @media only screen and (max-width: 1300px) {
        width: 50%;
        top: 1% !important;
        zoom: 0.9;
        margin: 6em;
        position: static;
        top: 10%;
        bottom: 40%;
        border-radius: 5px;
    
      }
    @media only screen and (max-width: 900px) {
      width: 60%;
      zoom: 0.95;
      margin: 5em;
      position: static;
      top: 10%;
      bottom: 40%;
      border-radius: 5px;
      
    }
    @media only screen and (max-width: 600px) {
      width: 90%;
      top: 1% !important;
      zoom: 0.9;
      margin: 6em;
      position: static;
      top: 10%;
      bottom: 40%;
      border-radius: 5px;
  
    }
  
  
    .clouse {
      height: 20px;
  
      svg {
        width: 20px;
        float: right;
        margin-top: -10px;
      }
    }
  
    .sign_in{
        font-weight: 700;
        display: flex;
        flex-direction: column;
        align-items: center;
        img{
            height: 60px;
        }
    }
    .sign_in_inputs{

        .sign_in_input{
            margin: 15px auto;
            width: 80%;
            p{
                font-weight: 600;
                color: $theBlue;
            }

            input{
                width: 100%;
                height: 50px;
               border: 0.4px solid $theBlue;
               padding: 8px;
               border-radius: 8px;
            }
        }

    }
  
  
    button {
      width: 180px;
      padding: 15px;
      background-color: $orange;
      color: white;
      border: none;
      border-radius: 8px;
      margin: 16px auto;
      display: flex;
      justify-content: center;
      font-weight: 700;
    }
  }